<template>
    <div class="vue-doc-wrapper">
        
        <div class="vue-doc-title">
          <h2 id="vue-doc-行内表单1" class="vue-doc-element">行内表单</h2>
<p class="vue-doc-element">设置属性 inline，表单元素可以水平排列。</p>

        </div>
        <div class="vue-doc-title-EN">
          <h2 id="vue-doc-buttonsize1" class="vue-doc-element">Button Size</h2>
<p class="vue-doc-element">Button</p>

        </div>

        


        <div class="vue-doc-content">
          
        </div>
        <div class="vue-doc-content-EN">
          
        </div>
        
          <div class="vue-doc-demo">
            <template>
    <Form ref="formInline" :model="formInline" :rules="ruleInline" inline>
        <FormItem prop="user">
            <Input type="text" v-model="formInline.user" placeholder="Username">
                <Icon type="ios-person-outline" slot="prepend"></Icon>
            </Input>
        </FormItem>
        <FormItem prop="password">
            <Input type="password" v-model="formInline.password" placeholder="Password">
                <Icon type="ios-locked-outline" slot="prepend"></Icon>
            </Input>
        </FormItem>
        <FormItem>
            <Button type="primary" @click="handleSubmit('formInline')">Signin</Button>
        </FormItem>
    </Form>
</template>
          </div>
            <div class="vue-doc-code" :class="{'vue-doc-code-show':showVueDocCode}">
              <div class="vue-doc-code-copy" @click="vueDocCodeCopy">{{vueDocCodeCopyText}}</div>
              <div class="vue-doc-code-content" ref="vueDocCodeBox">
                <pre class="vue-doc-element"><code class="lang-vue"><span class="hljs-tag">&lt;<span class="hljs-name">template</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">Form</span> <span class="hljs-attr">ref</span>=<span class="hljs-string">"formInline"</span> <span class="hljs-attr">:model</span>=<span class="hljs-string">"formInline"</span> <span class="hljs-attr">:rules</span>=<span class="hljs-string">"ruleInline"</span> <span class="hljs-attr">inline</span>&gt;</span>
        <span class="hljs-tag">&lt;<span class="hljs-name">FormItem</span> <span class="hljs-attr">prop</span>=<span class="hljs-string">"user"</span>&gt;</span>
            <span class="hljs-tag">&lt;<span class="hljs-name">Input</span> <span class="hljs-attr">type</span>=<span class="hljs-string">"text"</span> <span class="hljs-attr">v-model</span>=<span class="hljs-string">"formInline.user"</span> <span class="hljs-attr">placeholder</span>=<span class="hljs-string">"Username"</span>&gt;</span>
                <span class="hljs-tag">&lt;<span class="hljs-name">Icon</span> <span class="hljs-attr">type</span>=<span class="hljs-string">"ios-person-outline"</span> <span class="hljs-attr">slot</span>=<span class="hljs-string">"prepend"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">Icon</span>&gt;</span>
            <span class="hljs-tag">&lt;/<span class="hljs-name">Input</span>&gt;</span>
        <span class="hljs-tag">&lt;/<span class="hljs-name">FormItem</span>&gt;</span>
        <span class="hljs-tag">&lt;<span class="hljs-name">FormItem</span> <span class="hljs-attr">prop</span>=<span class="hljs-string">"password"</span>&gt;</span>
            <span class="hljs-tag">&lt;<span class="hljs-name">Input</span> <span class="hljs-attr">type</span>=<span class="hljs-string">"password"</span> <span class="hljs-attr">v-model</span>=<span class="hljs-string">"formInline.password"</span> <span class="hljs-attr">placeholder</span>=<span class="hljs-string">"Password"</span>&gt;</span>
                <span class="hljs-tag">&lt;<span class="hljs-name">Icon</span> <span class="hljs-attr">type</span>=<span class="hljs-string">"ios-locked-outline"</span> <span class="hljs-attr">slot</span>=<span class="hljs-string">"prepend"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">Icon</span>&gt;</span>
            <span class="hljs-tag">&lt;/<span class="hljs-name">Input</span>&gt;</span>
        <span class="hljs-tag">&lt;/<span class="hljs-name">FormItem</span>&gt;</span>
        <span class="hljs-tag">&lt;<span class="hljs-name">FormItem</span>&gt;</span>
            <span class="hljs-tag">&lt;<span class="hljs-name">Button</span> <span class="hljs-attr">type</span>=<span class="hljs-string">"primary"</span> @<span class="hljs-attr">click</span>=<span class="hljs-string">"handleSubmit('formInline')"</span>&gt;</span>Signin<span class="hljs-tag">&lt;/<span class="hljs-name">Button</span>&gt;</span>
        <span class="hljs-tag">&lt;/<span class="hljs-name">FormItem</span>&gt;</span>
    <span class="hljs-tag">&lt;/<span class="hljs-name">Form</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">template</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
    <span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
        data () {
            <span class="hljs-keyword">return</span> {
                <span class="hljs-attr">formInline</span>: {
                    <span class="hljs-attr">user</span>: <span class="hljs-string">''</span>,
                    <span class="hljs-attr">password</span>: <span class="hljs-string">''</span>
                },
                <span class="hljs-attr">ruleInline</span>: {
                    <span class="hljs-attr">user</span>: [
                        { <span class="hljs-attr">required</span>: <span class="hljs-literal">true</span>, <span class="hljs-attr">message</span>: <span class="hljs-string">'Please fill in the user name'</span>, <span class="hljs-attr">trigger</span>: <span class="hljs-string">'blur'</span> }
                    ],
                    <span class="hljs-attr">password</span>: [
                        { <span class="hljs-attr">required</span>: <span class="hljs-literal">true</span>, <span class="hljs-attr">message</span>: <span class="hljs-string">'Please fill in the password.'</span>, <span class="hljs-attr">trigger</span>: <span class="hljs-string">'blur'</span> },
                        { <span class="hljs-attr">type</span>: <span class="hljs-string">'string'</span>, <span class="hljs-attr">min</span>: <span class="hljs-number">6</span>, <span class="hljs-attr">message</span>: <span class="hljs-string">'The password length cannot be less than 6 bits'</span>, <span class="hljs-attr">trigger</span>: <span class="hljs-string">'blur'</span> }
                    ]
                }
            }
        },
        <span class="hljs-attr">methods</span>: {
            handleSubmit(name) {
                <span class="hljs-keyword">this</span>.$refs[name].validate(<span class="hljs-function">(<span class="hljs-params">valid</span>) =&gt;</span> {
                    <span class="hljs-keyword">if</span> (valid) {
                        <span class="hljs-keyword">this</span>.$Message.success(<span class="hljs-string">'Success!'</span>);
                    } <span class="hljs-keyword">else</span> {
                        <span class="hljs-keyword">this</span>.$Message.error(<span class="hljs-string">'Fail!'</span>);
                    }
                })
            }
        }
    }
</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>

</code></pre>

              </div>
              <div class="vue-doc-code-toggle" @click="vueDocCodeToggle">
                <span class="vue-doc-code-toggle-text"></>{{showVueDocCode?"隐藏代码":"显示代码"}}</span>
                <span class="vue-doc-code-toggle-text-EN"></>{{showVueDocCode?"Hide Code":"Show Code"}}</span>
              </div>
            </div>
          


    </div>
  </template>
  <script>
    
  
  import ClipboardJS from "clipboard";
  const mixin = {
    data(){
      return{
        showVueDocCode:false,
        vueDocCodeCopyText:"copy",
        vueDocCodeHeight:0
      }
    },
    methods: {
      vueDocCodeToggle() {
        this.showVueDocCode=!this.showVueDocCode;
        this.vueDocCodeHeight=this.vueDocCodeHeight||this.$refs.vueDocCodeBox.children[0].offsetHeight;
        this.$refs.vueDocCodeBox.style.height=this.showVueDocCode?(this.vueDocCodeHeight+50)+'px':'0px';
      },
      vueDocCodeCopy(){
        const clipboard = new ClipboardJS('.vue-doc-code-copy',{
          text:(trigger)=> {
                return trigger.nextElementSibling.textContent;
            }
        });
        clipboard.on('success', (e)=> {
            this.vueDocCodeCopyText="copy success!";
            setTimeout(()=>{
              this.vueDocCodeCopyText="copy";
            },1000);
            e.clearSelection();
            clipboard.destroy();
        });
      }
    },
    components:{
      
    }
  };
  export default { mixins: [mixin],
        data () {
            return {
                formInline: {
                    user: '',
                    password: ''
                },
                ruleInline: {
                    user: [
                        { required: true, message: 'Please fill in the user name', trigger: 'blur' }
                    ],
                    password: [
                        { required: true, message: 'Please fill in the password.', trigger: 'blur' },
                        { type: 'string', min: 6, message: 'The password length cannot be less than 6 bits', trigger: 'blur' }
                    ]
                }
            }
        },
        methods: {
            handleSubmit(name) {
                this.$refs[name].validate((valid) => {
                    if (valid) {
                        this.$Message.success('Success!');
                    } else {
                        this.$Message.error('Fail!');
                    }
                })
            }
        }
    }
</script>
  
  <style lang="less">
    @import "ivue-doc-loader/styles/code/default.css";
    @import "ivue-doc-loader/styles/default.css";
    .vue-doc-code-content{
      height:0;
      overflow:hidden;
      transition: height .3s;
      background:#f8f8f8;
    }
    .vue-doc-code-show .vue-doc-code-content{
      height:auto;
    }
    .vue-doc-content-EN,.vue-doc-title-EN,.vue-doc-submenu-list-EN,.vue-doc-code-toggle-text-EN{
      display:none;
    }
    .en-US .vue-doc-submenu-list-EN{
      display:block;
    }
    .en-US .vue-doc-submenu-list{
      display:none;
    }

    .en-US .vue-doc-content-EN{
      display:block;
    }
    .en-US .vue-doc-content{
      display:none;
    }

    .en-US .vue-doc-title-EN{
      display:block;
    }
    .en-US .vue-doc-title{
      display:none;
    }

    .en-US .vue-doc-code-toggle-text-EN{
      display:block;
    }
    .en-US .vue-doc-code-toggle-text{
      display:none;
    }

    .vue-doc-demo{
      padding: 20px;
      border:1px solid #ddd;
      border-bottom: 0;
      border-top-left-radius: 5px;
      border-top-right-radius: 5px;
    }
    .vue-doc-code{
      border: 1px solid #ddd;
      position: relative;
      padding-bottom: 50px;
      border-bottom-left-radius: 5px;
      border-bottom-right-radius: 5px;
    }
    .vue-doc-code-copy{
      display: none;
      position: absolute;
      right: 15px;
      font-weight: bold;
      top: 10px;
      cursor: pointer;
    }
    .vue-doc-code-show .vue-doc-code-copy{
      display: block;
    }
    .vue-doc-code-toggle{
      position: absolute;
      bottom: 0;
      left: 0;
      width:100%;
      font-weight: bold;
      height: 50px;
      line-height: 50px;
      text-align: center;
      cursor: pointer;
    }
    .vue-doc-code-show .vue-doc-code-toggle{
      border-top: 1px solid #ddd;
    }

    .vue-doc-code .vue-doc-code-content pre{
      border: 0;
      padding: 15px;
    }

    .vue-doc-wrapper{
      margin-bottom:50px;
    }



  </style>


