                <span class="hljs-tag">&lt;<span class="hljs-name">Checkbox</span> <span class="hljs-attr">label</span>=<span class="hljs-string">"Sleep"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">Checkbox</span>&gt;</span>
                <span class="hljs-tag">&lt;<span class="hljs-name">Checkbox</span> <span class="hljs-attr">label</span>=<span class="hljs-string">"Run"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">Checkbox</span>&gt;</span>
                <span class="hljs-tag">&lt;<span class="hljs-name">Checkbox</span> <span class="hljs-attr">label</span>=<span class="hljs-string">"Movie"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">Checkbox</span>&gt;</span>
