<template>
    <div class="vue-doc-wrapper">
        <div class="vue-doc-submenu">
          <ul class='vue-doc-submenu-list'><li><a href="#vue-doc-行内表单1">行内表单</a></li><li><a href="#vue-doc-表单控件1">表单控件</a></li><li><a href="#vue-doc-对齐方式1">对齐方式</a></li><li><a href="#vue-doc-表单验证1">表单验证</a></li><li><a href="#vue-doc-自定义验证1">自定义验证</a></li><li><a href="#vue-doc-动态增减表单项1">动态增减表单项</a></li></ul><ul class='vue-doc-submenu-list-EN'><li><a href="#vue-doc-ButtonSize1">Button Size</a></li><li><a href="#vue-doc-ButtonSize1">Button Size</a></li><li><a href="#vue-doc-ButtonSize1">Button Size</a></li><li><a href="#vue-doc-ButtonSize1">Button Size</a></li><li><a href="#vue-doc-ButtonSize1">Button Size</a></li><li><a href="#vue-doc-ButtonSize1">Button Size</a></li></ul>
        </div>
        <div class="vue-doc-title">
          <h1 id="vue-doc-form表单1" class="vue-doc-element">Form 表单</h1>
<p class="vue-doc-element">具有数据收集、校验和提交功能的表单，包含复选框、单选框、输入框、下拉选择框等元素。</p>

        </div>
        <div class="vue-doc-title-EN">
          <h1 id="vue-doc-button1" class="vue-doc-element">Button</h1>
<p class="vue-doc-element">Basic component that is used to trigger bussiness logic.</p>

        </div>

        <div class="vue-doc-components">
          <demobasic></demobasic>
<democomponennt></democomponennt>
<demoalign></demoalign>
<demovalidate></demovalidate>
<democutomValidate></democutomValidate>
<demodynamic></demodynamic>
        </div>


        <div class="vue-doc-content">
          <blockquote class="vue-doc-element">
<p class="vue-doc-element">W3C 标准中有如下规定：
当一个 form 元素中只有一个输入框时，在该输入框中按下回车应提交该表单。如果希望阻止这一默认行为，可以在 Form 标签上添加 @submit.native.prevent。</p>
</blockquote>
<h2 id="vue-doc-api1" class="vue-doc-element">API</h2>
<h3 id="vue-doc-formprops1_1" class="vue-doc-element">Form props</h3>
<table class="vue-doc-element">
<thead>
<tr>
<th>属性</th>
<th>说明</th>
<th>类型</th>
<th>默认值</th>
</tr>
</thead>
<tbody>
<tr>
<td>model</td>
<td>表单数据对象</td>
<td>Object</td>
<td>-</td>
</tr>
<tr>
<td>rules</td>
<td>表单验证规则，具体配置查看 async-validator</td>
<td>Object</td>
<td>-</td>
</tr>
<tr>
<td>inline</td>
<td>是否开启行内表单模式</td>
<td>Boolean</td>
<td>false</td>
</tr>
<tr>
<td>label-position</td>
<td>表单域标签的位置，可选值为 left、right、top</td>
<td>String</td>
<td>right</td>
</tr>
<tr>
<td>label-width</td>
<td>表单域标签的宽度，所有的 FormItem 都会继承 Form 组件的 label-width 的值</td>
<td>Number</td>
<td>-</td>
</tr>
<tr>
<td>show-message</td>
<td>是否显示校验错误信息</td>
<td>Boolean</td>
<td>true</td>
</tr>
<tr>
<td>autocomplete</td>
<td>原生的 autocomplete 属性，可选值为 off 或 on</td>
<td>String</td>
<td>off</td>
</tr>
</tbody>
</table>
<h3 id="vue-doc-formmethods1_2" class="vue-doc-element">Form methods</h3>
<table class="vue-doc-element">
<thead>
<tr>
<th>方法名</th>
<th>说明</th>
<th>参数</th>
</tr>
</thead>
<tbody>
<tr>
<td>validate</td>
<td>对整个表单进行校验，参数为检验完的回调，会返回一个 Boolean 表示成功与失败，支持 Promise</td>
<td>callback</td>
</tr>
<tr>
<td>validateField</td>
<td>对部分表单字段进行校验的方法，参数1为需校验的 prop，参数2为检验完回调，返回错误信息</td>
<td>callback</td>
</tr>
<tr>
<td>resetFields</td>
<td>对整个表单进行重置，将所有字段值重置为空并移除校验结果</td>
<td>无</td>
</tr>
<tr>
<td>resetValidate</td>
<td>参数为String重置一个item,多个使用数组,为空重置所有</td>
<td>prop</td>
</tr>
<tr>
<td>resetFields</td>
<td>规则同上</td>
<td>prop</td>
</tr>
</tbody>
</table>
<h3 id="vue-doc-formitemprops1_3" class="vue-doc-element">FormItem props</h3>
<table class="vue-doc-element">
<thead>
<tr>
<th>属性</th>
<th>说明</th>
<th>类型</th>
<th>默认值</th>
</tr>
</thead>
<tbody>
<tr>
<td>prop</td>
<td>对应表单域 model 里的字段</td>
<td>String</td>
<td>-</td>
</tr>
<tr>
<td>label</td>
<td>标签文本</td>
<td>String</td>
<td>-</td>
</tr>
<tr>
<td>label-width</td>
<td>表单域标签的的宽度</td>
<td>Number</td>
<td>-</td>
</tr>
<tr>
<td>label-for</td>
<td>指定原生的 label 标签的 for 属性，配合控件的 element-id 属性，可以点击 label 时聚焦控件。</td>
<td>String</td>
<td>-</td>
</tr>
<tr>
<td>required</td>
<td>是否必填，如不设置，则会根据校验规则自动生成</td>
<td>Boolean</td>
<td>-</td>
</tr>
<tr>
<td>rules</td>
<td>表单验证规则</td>
<td>Object / Array</td>
<td>-</td>
</tr>
<tr>
<td>error</td>
<td>表单域验证错误信息, 设置该值会使表单验证状态变为error，并显示该错误信息</td>
<td>String</td>
<td>-</td>
</tr>
<tr>
<td>show-message</td>
<td>是否显示校验错误信息</td>
<td>Boolean</td>
<td>true</td>
</tr>
</tbody>
</table>
<h3 id="vue-doc-formitemslot1_4" class="vue-doc-element">FormItem slot</h3>
<table class="vue-doc-element">
<thead>
<tr>
<th>名称</th>
<th>说明</th>
</tr>
</thead>
<tbody>
<tr>
<td>无</td>
<td>内容</td>
</tr>
<tr>
<td>label</td>
<td>label 内容</td>
</tr>
</tbody>
</table>

        </div>
        <div class="vue-doc-content-EN">
          <h2 id="vue-doc-api1" class="vue-doc-element">API</h2>
<table class="vue-doc-element">
<thead>
<tr>
<th>Attribute</th>
<th style="text-align:right">Explanation</th>
<th>Type</th>
<th>Default</th>
</tr>
</thead>
<tbody>
<tr>
<td>type</td>
<td style="text-align:right">Button type, options include primary、ghost、dashed、text、info、success、warning、error, optional</td>
<td>String</td>
<td>-</td>
</tr>
</tbody>
</table>

        </div>
        


    </div>
  </template>
  <script>
      
  import demobasic from './demo/basic.md';
import democomponennt from './demo/componennt.md';
import demoalign from './demo/align.md';
import demovalidate from './demo/validate.md';
import democutomValidate from './demo/cutomValidate.md';
import demodynamic from './demo/dynamic.md';
  import ClipboardJS from "clipboard";
  const mixin = {
    data(){
      return{
        showVueDocCode:false,
        vueDocCodeCopyText:"copy",
        vueDocCodeHeight:0
      }
    },
    methods: {
      vueDocCodeToggle() {
        this.showVueDocCode=!this.showVueDocCode;
        this.vueDocCodeHeight=this.vueDocCodeHeight||this.$refs.vueDocCodeBox.children[0].offsetHeight;
        this.$refs.vueDocCodeBox.style.height=this.showVueDocCode?(this.vueDocCodeHeight+50)+'px':'0px';
      },
      vueDocCodeCopy(){
        const clipboard = new ClipboardJS('.vue-doc-code-copy',{
          text:(trigger)=> {
                return trigger.nextElementSibling.textContent;
            }
        });
        clipboard.on('success', (e)=> {
            this.vueDocCodeCopyText="copy success!";
            setTimeout(()=>{
              this.vueDocCodeCopyText="copy";
            },1000);
            e.clearSelection();
            clipboard.destroy();
        });
      }
    },
    components:{
      demobasic,democomponennt,demoalign,demovalidate,democutomValidate,demodynamic
    }
  };
  
      export default {
        mixins:[mixin]
      }
      </script>
  
  <style lang="less">
    @import "ivue-doc-loader/styles/code/default.css";
    @import "ivue-doc-loader/styles/default.css";
    .vue-doc-code-content{
      height:0;
      overflow:hidden;
      transition: height .3s;
      background:#f8f8f8;
    }
    .vue-doc-code-show .vue-doc-code-content{
      height:auto;
    }
    .vue-doc-content-EN,.vue-doc-title-EN,.vue-doc-submenu-list-EN,.vue-doc-code-toggle-text-EN{
      display:none;
    }
    .en-US .vue-doc-submenu-list-EN{
      display:block;
    }
    .en-US .vue-doc-submenu-list{
      display:none;
    }

    .en-US .vue-doc-content-EN{
      display:block;
    }
    .en-US .vue-doc-content{
      display:none;
    }

    .en-US .vue-doc-title-EN{
      display:block;
    }
    .en-US .vue-doc-title{
      display:none;
    }

    .en-US .vue-doc-code-toggle-text-EN{
      display:block;
    }
    .en-US .vue-doc-code-toggle-text{
      display:none;
    }

    .vue-doc-demo{
      padding: 20px;
      border:1px solid #ddd;
      border-bottom: 0;
      border-top-left-radius: 5px;
      border-top-right-radius: 5px;
    }
    .vue-doc-code{
      border: 1px solid #ddd;
      position: relative;
      padding-bottom: 50px;
      border-bottom-left-radius: 5px;
      border-bottom-right-radius: 5px;
    }
    .vue-doc-code-copy{
      display: none;
      position: absolute;
      right: 15px;
      font-weight: bold;
      top: 10px;
      cursor: pointer;
    }
    .vue-doc-code-show .vue-doc-code-copy{
      display: block;
    }
    .vue-doc-code-toggle{
      position: absolute;
      bottom: 0;
      left: 0;
      width:100%;
      font-weight: bold;
      height: 50px;
      line-height: 50px;
      text-align: center;
      cursor: pointer;
    }
    .vue-doc-code-show .vue-doc-code-toggle{
      border-top: 1px solid #ddd;
    }

    .vue-doc-code .vue-doc-code-content pre{
      border: 0;
      padding: 15px;
    }

    .vue-doc-wrapper{
      margin-bottom:50px;
    }



  </style>


