                <span class="hljs-attr">formDynamic</span>: {
                    <span class="hljs-attr">items</span>: [
                        {
                            <span class="hljs-attr">value</span>: <span class="hljs-string">''</span>,
                            <span class="hljs-attr">index</span>: <span class="hljs-number">1</span>,
                            <span class="hljs-attr">status</span>: <span class="hljs-number">1</span>
                        }
                    ]
                }
            }
        },
        <span class="hljs-attr">methods</span>: {
            handleSubmit (name) {
                <span class="hljs-keyword">this</span>.$refs[name].validate(<span class="hljs-function">(<span class="hljs-params">valid</span>) =&gt;</span> {
                    <span class="hljs-keyword">if</span> (valid) {
                        <span class="hljs-keyword">this</span>.$Message.success(<span class="hljs-string">'Success!'</span>);
                    } <span class="hljs-keyword">else</span> {
                        <span class="hljs-keyword">this</span>.$Message.error(<span class="hljs-string">'Fail!'</span>);
                    }
                })
            },
            handleReset (name) {
                <span class="hljs-keyword">this</span>.$refs[name].resetFields();
