        data () {
            <span class="hljs-keyword">const</span> validatePass = <span class="hljs-function">(<span class="hljs-params">rule, value, callback</span>) =&gt;</span> {
                <span class="hljs-keyword">if</span> (value === <span class="hljs-string">''</span>) {
                    callback(<span class="hljs-keyword">new</span> <span class="hljs-built_in">Error</span>(<span class="hljs-string">'Please enter your password'</span>));
                } <span class="hljs-keyword">else</span> {
                    <span class="hljs-keyword">if</span> (<span class="hljs-keyword">this</span>.formCustom.passwdCheck !== <span class="hljs-string">''</span>) {
